@media (max-width: 750px) {
  .table-header {
    flex-direction: column !important;
  }
  .students-title {
    font-size: 14px !important;
    text-align: left !important;

    align-self: flex-start !important;
  }
  .form-header-input {
    width: 150px !important;
    height: 25px !important;
  }
  .table-header-button {
    width: 100px !important;
    height: 25px !important;
    font-size: 10px !important;
  }
  .table-header-right {
    justify-content: space-between !important;
    width: 100%;
  }

  .is-mobile-add-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 15em;
  }

  .is-mobile-add-container > td > input {
    width: 80% !important;
    height: 20px;
  }
}

.app-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 1rem;
}

.confirm-alert-container {
  width: 2em;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  font-size: 32px;
}

th {
  background-color: #f8f8f8;
}

td {
  background-color: #fff;
  border: none;
  padding: 8px;
}

tbody > tr {
  border-top: 10px solid #f8f8f8;
  border-bottom: 10px solid #f8f8f8;
  height: 85px;

  background: transparent;
  border-left: none;
  text-align: center;
}

tbody > td {
  text-align: left;
}

.table-item {
}

thead > tr {
  font-size: 12px;
  font-weight: 600;
  line-height: 14.5px;
  color: #acacac;
  height: 50px;
}

form {
  display: flex;
  gap: 5px;
}

form td:last-child {
  text-align: center;
}

form * {
  font-size: 13px;
}

.table-form {
  display: flex;
  flex-direction: column;
}

.table-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #f8f8f8;
  height: 60px;
  align-items: center;
  padding: 0px 10px;
}

.students-title {
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
}

.inputwithSearch {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 3em;
}

.table-header-right {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.form-header-input {
  width: 212px;
  height: 37px;
  text-decoration: none;
  border: 1px solid #e5e5e5;
  border-radius: 8px;
  padding: 0 10px;
}

.form-header-input::placeholder {
  font-weight: 400;
  font-size: 14px;

  line-height: 17px;
}

.form-header-searchicon {
  margin-left: -2em;
}

.table-header-button {
  width: 199px;
  height: 44px;
  border-radius: 4px;
  background-color: #feaf00;
  align-items: center;
  justify-content: center;
  display: flex;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  color: white;
  cursor: pointer;
}

.add-table-button {
  background-color: #feaf00;
  color: white;
  font-weight: 500;
  padding: 10px 20px;
  border-radius: 3px;
  cursor: pointer;
}

.add-table-button:hover {
  background-color: #ffca59;
}
